.footer_box {
    width: 100%;
    // height: 400px;
    position: relative;
    .container {
        width: 1200px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        z-index: 1;
            .left {
                ul {
                    display: flex;
                    align-items: center;
                    li {
                        margin-right: 62px;
                        cursor: pointer;
                        p {
                            font-size: 17px;
                        }
                    }
                }
                .contact_title {
                    color: #A4A4A4;
                }
            }
            .right {
                display: flex;
                ul {
                    display: flex;
                    align-items: center;
                    li {
                        margin-right: 24px;
                        img {
                            cursor: pointer;
                        }
                    }
                }
            }
    }
    .bg_box {
        height: 794.57px;
        width: 795.2px;
        background: url("../../../assets/home/bg-gradient-img.png") no-repeat center;
        background-size: cover;
        position: absolute;
        left: 0;
        top: -600px;
        z-index: -1;
    }
}